@mixin pseudoelement($width, $height) {
    content : '';
    display : block;
    position: absolute;
    width   : $width;
    height  : $height;
}

@mixin flexible($jf, $ai, $fd:row) {
    display        : flex;
    flex-direction : $fd;
    justify-content: $jf;
    align-items    : $ai;
}

@mixin center-width($percentage){
    width: $percentage;
    margin-left: auto;
    margin-right: auto;
}